@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }

.col-md-auto {
  flex: 0 0 auto;
  width: auto;
  max-width: none; }

.col-md-1 {
  flex: 0 0 8.333333%;
  max-width: 8.333333%; }

.col-md-2 {
  flex: 0 0 16.666667%;
  max-width: 16.666667%; }

.col-md-3 {
  flex: 0 0 25%;
  max-width: 25%; }

.col-md-4 {
  flex: 0 0 33.333333%;
  max-width: 33.333333%; }

.col-md-5 {
  flex: 0 0 41.666667%;
  max-width: 41.666667%; }

.col-md-6 {
  flex: 0 0 50%;
  max-width: 50%; }

.col-md-7 {
  flex: 0 0 58.333333%;
  max-width: 58.333333%; }

.col-md-8 {
  flex: 0 0 66.666667%;
  max-width: 66.666667%; }

.col-md-9 {
  flex: 0 0 75%;
  max-width: 75%; }

.col-md-10 {
  flex: 0 0 83.333333%;
  max-width: 83.333333%; }

.col-md-11 {
  flex: 0 0 91.666667%;
  max-width: 91.666667%; }

.col-md-12 {
  flex: 0 0 100%;
  max-width: 100%; }

@media print {
  .container {
    width: 90vw;
    padding: 0;
    margin: 0; } }

@media (min-width: 576px) {
  .container {
    max-width: 540px; } }

@media (min-width: 768px) {
  .container {
    max-width: 720px; } }

@media (min-width: 992px) {
  .container {
    max-width: 960px; } }

@media (min-width: 1200px) {
  .container {
    max-width: 1140px; } }

.app {
  color: #1a112e; }
  .app .head-title {
    margin-bottom: 0;
    padding-bottom: 5px;
    margin-bottom: 10px;
    border-bottom: 1px solid #a4a8b2; }
  .app .pd-btm {
    padding-bottom: 20px; }
  .app .let-spc {
    letter-spacing: 1px; }
  .app__header__info {
    margin-top: 15px; }
    .app__header__info h1 {
      margin-bottom: 0px;
      letter-spacing: 1px; }
    .app__header__info span:first-child {
      color: #a4a8b2; }
    .app__header__info span {
      color: #1a112e; }
    .app__header__info .personal-info {
      display: inline-block; }
    .app__header__info .social-items {
      display: inline-flex;
      float: right;
      height: 6em; }
      .app__header__info .social-items .social-item {
        margin: auto 20px; }
        .app__header__info .social-items .social-item:last-child {
          margin-right: 0; }
        .app__header__info .social-items .social-item img {
          width: 30px;
          -webkit-filter: grayscale(100%);
          filter: grayscale(100%); }
          .app__header__info .social-items .social-item img:hover {
            padding-bottom: 10px;
            border-bottom: 4px solid #4d4bf4;
            -webkit-filter: grayscale(0%);
            /* Safari 6.0 - 9.0 */
            filter: grayscale(0%); }
  .app__experience__item h4 {
    margin-bottom: 5px;
    display: inline; }
    .app__experience__item h4 a {
      color: #1a112e;
      text-decoration: none; }
      .app__experience__item h4 a:hover {
        text-decoration: underline; }
  .app__experience__item p {
    margin: unset;
    display: inline; }
    .app__experience__item p span {
      float: right; }
  .app__experience__item ul {
    margin-top: 10px; }
  .app__education h4 span {
    font-weight: 400;
    float: right; }
  .app__projects__item p b a {
    color: #1a112e; }
  .app__certificate span {
    color: #a4a8b2; }
  .app__award p {
    margin-bottom: 0; }
    .app__award p span {
      float: right; }

@media screen and (min-width: 320px) and (max-width: 1060px) {
  .app {
    font-size: 0.67em !important; }
    .app .social-items .social-item {
      margin: auto 7px !important; }
      .app .social-items .social-item img {
        width: 12px !important; } }

